import * as React from "react"
// Import Libraries
import { useLocomotiveScroll } from 'react-locomotive-scroll'



const GoUp = () => {


    // const optionScroll:ScrollToOptions = {
    //     top: 0,
    //     left: 0,
    //     behavior: 'smooth'
    // }


    // GO TO UP
    function goToUp( e:React.MouseEvent ) {
        e.preventDefault()
        scroll.scrollTo('top')
        // window.scroll(optionScroll)
    }



    // VARIABLES
    const { scroll } = useLocomotiveScroll()



    return (
        <section className="goUp" onClick={ e => goToUp(e) }>
            <span className="goUp__arrow"></span>
            <p className="goUp__text">Volver al inicio</p>
        </section>
    )
}



export default GoUp